import TronWeb from 'tronweb';
import BigNumber from 'bignumber.js';
import store from '../store';
import { showSnackbar } from '../actions/snackbar';
import { SNACKBAR_ERROR } from '../components/Common/GameSnackbar';

import {
  FOUNDATION_ADDRESS,
  initContract,
  injectTronWeb,
  sunToTrx,
  TRON_WEB_STATE_LOCKED,
  TRON_WEB_STATE_NOT_MAINNET,
  weiToToken,
} from './tronCommon';
import { toTruncateDown } from './math';

const TRONGRID_API = 'https://api.trongrid.io';

// MAINNET
const CONTRACT_ADDRESS = '413f100fbdd26bff25982c35cdadc2f2e6837516cd';

// TESTNET
// const CONTRACT_ADDRESS = '413084e0c71893d69c2ec1e3fb7701a479da2c9c05';

let contract;
let tronWebState;
let tronWeb;

export default {
  async initTronWeb(address) {
    tronWeb = new TronWeb(TRONGRID_API, TRONGRID_API, TRONGRID_API);
    tronWeb.defaultAddress = {
      hex: tronWeb.address.toHex(address),
      base58: FOUNDATION_ADDRESS,
    };

    tronWebState = await injectTronWeb(tronWeb);
    contract = await initContract(
      TRON_WEB_STATE_LOCKED,
      tronWeb,
      CONTRACT_ADDRESS
    );
    if (!contract) {
      tronWebState = TRON_WEB_STATE_NOT_MAINNET;
      store.dispatch(
        showSnackbar('message.needToChangeNetwork', SNACKBAR_ERROR)
      );
    }
    return tronWebState;
  },

  async getPlayerData(address) {
    if (!this.isValidAddress(address)) {
      return null;
    }

    const myTokens = await contract.myTokens().call();
    const myDividends = await contract.myDividends().call();
    const myRoundStats = await contract
      .getPlayerInfoCurrentRound(address)
      .call();
    const myReferrals = await contract.myReferrals().call();
    return {
      myTokens: myTokens
        ? toTruncateDown(weiToToken(new BigNumber(myTokens).toNumber()), 6)
        : 0,
      myDividends: myDividends
        ? sunToTrx(new BigNumber(myDividends).toNumber())
        : 0,
      myReferrals: myReferrals
        ? sunToTrx(new BigNumber(myReferrals).toNumber())
        : 0,
      myRoundBuy: myRoundStats
        ? sunToTrx(new BigNumber(myRoundStats[0]).toNumber())
        : 0,
      myRoundSell: myRoundStats
        ? sunToTrx(new BigNumber(myRoundStats[1]).toNumber())
        : 0,
    };
  },

  async balanceOf(address) {
    const balance = await contract.balanceOf(address).call();
    return balance;
  },

  isValidAddress(address) {
    return address && tronWeb.isAddress(address);
  },
};
