import React, { Component } from 'react';
import { connect } from 'react-redux';
import { withLocalize } from 'react-localize-redux';
import { renderToStaticMarkup } from 'react-dom/server';

import MuiThemeProvider from '@material-ui/core/styles/MuiThemeProvider';

import { setScreen } from '../actions/app';
import customTheme from './Common/customTheme';
import AppBar from './Common/GameAppBar';
import Snackbar from './Common/GameSnackbar';
import RoutingScreen from './Common/RoutingScreen';
import Footer from './Common/Footer';
import './App.css';
import Tracker from '../utils/tracker';
import { parseGameRoute } from '../utils/parseQueryString';

class App extends Component {
  constructor(props) {
    super(props);

    this.props.initialize({
      languages: [
        { name: 'English', code: 'en' },
        { name: 'Chinese', code: 'cn' },
      ],
      options: { renderToStaticMarkup },
    });
  }

  componentDidMount() {
    this.initTracker();
    this.initScreen();
  }

  initScreen = () => {
    const { setScreen } = this.props;
    const screen = parseGameRoute();
    setScreen(screen);
  };

  initTracker = () => {
    Tracker.initialize();
    Tracker.pageview('/app');
  };

  render() {
    return (
      <MuiThemeProvider theme={customTheme}>
        <div className="App">
          <AppBar />
          <div id="body">
            <RoutingScreen />
          </div>
          <Footer />
          <Snackbar />
        </div>
      </MuiThemeProvider>
    );
  }
}

const mapDispatchToProps = {
  setScreen,
};

export default connect(
  null,
  mapDispatchToProps
)(withLocalize(App));
