import React, { Component } from 'react';
import { connect } from 'react-redux';

import Toolbar from '@material-ui/core/Toolbar/Toolbar';
import IconButton from '@material-ui/core/IconButton/IconButton';
import AppBar from '@material-ui/core/AppBar/AppBar';
import MenuIcon from '@material-ui/icons/Menu';
import Drawer from '@material-ui/core/Drawer/Drawer';
import Divider from '@material-ui/core/Divider/Divider';
import List from '@material-ui/core/List/List';
import ListItem from '@material-ui/core/ListItem/ListItem';
import { withLocalize, Translate } from 'react-localize-redux';
import Dialog from '@material-ui/core/Dialog/Dialog';
import Transition from './Transition';
import Tooltip from '@material-ui/core/Tooltip/Tooltip';
import CloseIcon from '@material-ui/icons/Close';
import Typography from '@material-ui/core/Typography/Typography';

import Lockr from 'lockr';

import siteTranslations from '../../translations/translate.json';

import { SCREEN_LANDING, SCREEN_VAULT, SCREEN_VAULT_SUPER,SCREEN_VAULT_LIGHTNING, setScreen } from '../../actions/app';

const TT_URL = 'https://twitter.com/trondexorg';
const TG_URL = 'https://t.me/trondex';

class GameAppBar extends Component {
  constructor(props) {
    super(props);

    const languages = [
      { name: 'English', code: 'en' },
      { name: '', code: 'cn' },
    ];
    this.state = {
      languages,
      mobileOpen: false,
      anchorEl: null,
      wechatOpen: false,
    };

    this.props.addTranslation(siteTranslations);
  }

  async componentDidMount() {
    const activeLanguageCode = Lockr.get('language') || 'en';
    this.props.setActiveLanguage(activeLanguageCode);
  }

  setActiveLanguage = code => {
    this.props.setActiveLanguage(code);
    Lockr.set('language', code);
    this.setState({ mobileOpen: false });
  };

  getFlagUrl = code => {
    return `images/misc/${code}.svg`;
  };

  getLogoImage = () => {
    return `images/misc/logo_white.png`;
  };

  navigateTo = screen => {
    const { setScreen } = this.props;
    this.setState({ mobileOpen: false });
    setScreen(screen);
  };

  home = () => {
    this.navigateTo(SCREEN_LANDING);
  };

  handleDrawerToggle = () => {
    this.setState(state => ({ mobileOpen: !state.mobileOpen }));
  };

  renderWechat() {
    const { wechatOpen } = this.state;

    return (
      <Dialog
        open={wechatOpen}
        onClose={this.closeWechat}
        TransitionComponent={Transition}
      >
        <AppBar position="static" className="wechat-appbar">
          <Toolbar>
            <Tooltip title="Close">
              <IconButton
                color="inherit"
                onClick={this.closeWechat}
                aria-label="Close"
              >
                <CloseIcon />
              </IconButton>
            </Tooltip>
            <Typography variant="h6" color="inherit">
              <Translate id="max.joinWechat" />
            </Typography>
          </Toolbar>
        </AppBar>
      </Dialog>
    );
  }

  openWechat = () => {
    this.setState({ wechatOpen: true });
  };

  closeWechat = () => {
    this.setState({ wechatOpen: false });
  };

  renderDesktop() {
    const { languages, anchorEl } = this.state;
    return (
      <div className="tool-bar desktop-app-bar">
        <div className="logo-container">
          <IconButton
            color="inherit"
            aria-label="logo"
            disableRipple={true}
            onClick={this.home}
          >
            <img
              src={this.getLogoImage()}
              className="logo-medium"
              alt="vault"
            />
          </IconButton>
        </div>

        <div>
          {/* <IconButton
            className="contract-menu-button"
            color="inherit"
            aria-label={<Translate id="max.invest" />}
            disableRipple={true}
            aria-owns={anchorEl ? 'contracts-menu' : undefined}
            href="#section_invest"
          >
            <span className="contracts-menu gold">
              <Translate id="max.invest" />
            </span>
          </IconButton>

          <IconButton
            className="contract-menu-button"
            color="inherit"
            aria-label={<Translate id="max.stats" />}
            disableRipple={true}
            aria-owns={anchorEl ? 'contracts-menu' : undefined}
            href="#section_stats"
          >
            <span className="contracts-menu">
              <Translate id="max.stats" />
            </span>
          </IconButton>

          <IconButton
            className="contract-menu-button"
            color="inherit"
            aria-label={<Translate id="max.myaccount" />}
            disableRipple={true}
            aria-owns={anchorEl ? 'contracts-menu' : undefined}
            href="#section_myaccount"
          >
            <span className="contracts-menu">
              <Translate id="max.myaccount" />
            </span>
          </IconButton>

          <IconButton
            className="contract-menu-button"
            color="inherit"
            aria-label={<Translate id="max.roadmap" />}
            disableRipple={true}
            aria-owns={anchorEl ? 'contracts-menu' : undefined}
            href="#section_faq"
          >
            <span className="contracts-menu">
              <Translate id="max.faq" />
            </span>
          </IconButton> */}

          <IconButton
            className="contract-menu-button"
            color="inherit"
            aria-label={<Translate id="max.lightningroi" />}
            disableRipple={true}
            aria-owns={anchorEl ? 'contracts-menu' : undefined}
            onClick={() => this.navigateTo(SCREEN_VAULT_LIGHTNING)}
          >
            <span className="contracts-menu">
              <Translate id="max.lightningroi" />
            </span>
          </IconButton>

          <IconButton
            className="contract-menu-button"
            color="inherit"
            aria-label={<Translate id="max.regularroi" />}
            disableRipple={true}
            aria-owns={anchorEl ? 'contracts-menu' : undefined}
            onClick={() => this.navigateTo(SCREEN_VAULT)}
          >
            <span className="contracts-menu">
              <Translate id="max.regularroi" />
            </span>
          </IconButton>


          <IconButton
            className="contract-menu-button"
            color="inherit"
            aria-label={<Translate id="max.superroi" />}
            disableRipple={true}
            aria-owns={anchorEl ? 'contracts-menu' : undefined}
            onClick={() => this.navigateTo(SCREEN_VAULT_SUPER)}
          >
            <span className="contracts-menu">
              <Translate id="max.superroi" />
            </span>
          </IconButton>

          {languages.map(lang => (
            <IconButton
              id={lang.code}
              key={lang.code}
              color="inherit"
              disableRipple={true}
              onClick={() => this.setActiveLanguage(lang.code)}
            >
              <img
                src={this.getFlagUrl(lang.code)}
                className="image-flag"
                alt="Telegram"
              />
            </IconButton>
          ))}

          <span className="social">
            <IconButton
              className="social-button"
              href={TT_URL}
              disableRipple={true}
              rel="noopener noreferrer"
              target="_blank"
            >
              <img
                src="images/social/twitter.png"
                className="image-flag"
                alt="Twitter"
              />
            </IconButton>
          </span>
          <span className="social">
            <IconButton
              className="social-button"
              href={TG_URL}
              disableRipple={true}
              rel="noopener noreferrer"
              target="_blank"
            >
              <img
                src="images/social/telegram.png"
                className="image-flag"
                alt="Telegram"
              />
            </IconButton>
          </span>
        </div>
      </div>
    );
  }

  renderMobile() {
    const { languages } = this.state;
    return (
      <div className="tool-bar mobile-app-bar">
        <IconButton
          color="inherit"
          aria-label="Open drawer"
          disableRipple={true}
          onClick={this.handleDrawerToggle}
        >
          <MenuIcon />
        </IconButton>
        <div className="logo-container">
          <IconButton
            color="inherit"
            disableRipple={true}
            aria-label={<Translate id="max.tronmax" />}
            onClick={this.home}
          >
            <span>
              <Translate id="max.tronmax" />
            </span>
          </IconButton>
        </div>
        <div className="mobile-app-bar-right" />
        <Drawer
          container={this.props.container}
          variant="temporary"
          anchor="left"
          open={this.state.mobileOpen}
          onClose={this.handleDrawerToggle}
          className="drawer"
        >
          <div className="drawer-menu">
            <List>
              <Divider />
              <ListItem button key="github" className="drawer-item">
                {languages.map(lang => (
                  <ListItem
                    button
                    key={lang.code}
                    className="drawer-item"
                    disableRipple={true}
                    onClick={() => this.setActiveLanguage(lang.code)}
                  >
                    <img
                      src={this.getFlagUrl(lang.code)}
                      className="image-flag"
                      alt="Telegram"
                    />
                  </ListItem>
                ))}
                <span className="social">
                  <IconButton
                    disableRipple={true}
                    className="social-button"
                    href={TT_URL}
                    rel="noopener noreferrer"
                    target="_blank"
                  >
                    <img
                      src="images/social/twitter.png"
                      className="image-flag"
                      alt="Twitter"
                    />
                  </IconButton>
                </span>
                <span className="social">
                  <IconButton
                    className="social-button"
                    href={TG_URL}
                    rel="noopener noreferrer"
                    target="_blank"
                  >
                    <img
                      src="images/social/telegram.png"
                      className="image-flag"
                      alt="Telegram"
                    />
                  </IconButton>
                </span>
              </ListItem><Divider />
              <ListItem button key="github" className="drawer-item">
                <IconButton
                  className="contract-menu-button"
                  color="inherit"
                  aria-label={<Translate id="max.lightningroi" />}
                  disableRipple={true}
                  onClick={() => this.navigateTo(SCREEN_VAULT_LIGHTNING)}
                >
                  <span className="contracts-menu">
                    <Translate id="max.lightningroi" />
                  </span>
                </IconButton>
              </ListItem>
              <Divider />
              <ListItem button key="github" className="drawer-item">
                <IconButton
                  className="contract-menu-button"
                  color="inherit"
                  aria-label={<Translate id="max.regularroi" />}
                  disableRipple={true}
                  onClick={() => this.navigateTo(SCREEN_VAULT)}
                >
                  <span className="contracts-menu">
                    <Translate id="max.regularroi" />
                  </span>
                </IconButton>
              </ListItem>
              <Divider />
              <ListItem button key="github" className="drawer-item">
                <IconButton
                  className="contract-menu-button"
                  color="inherit"
                  aria-label={<Translate id="max.superroi" />}
                  disableRipple={true}
                  onClick={() => this.navigateTo(SCREEN_VAULT_SUPER)}
                >
                  <span className="contracts-menu">
                    <Translate id="max.superroi" />
                  </span>
                </IconButton>
              </ListItem>
            </List>
          </div>
        </Drawer>
      </div>
    );
  }

  render() {
    return (
      <AppBar position="sticky">
        <Toolbar>
          {this.renderDesktop()}
          {this.renderMobile()}
        </Toolbar>
      </AppBar>
    );
  }
}

const mapDispatchToProps = {
  setScreen,
};

export default connect(
  null,
  mapDispatchToProps
)(withLocalize(GameAppBar));
