import React from 'react';
import { connect } from 'react-redux';
import { CopyToClipboard } from 'react-copy-to-clipboard';
import Countdown from 'react-countdown-now';
import moment from 'moment';
import { MuiThemeProvider } from '@material-ui/core/styles';
import { withStyles } from '@material-ui/core/styles';
import Grid from '@material-ui/core/Grid';
import Button from '@material-ui/core/Button';
import TextField from '@material-ui/core/TextField';
import IconButton from '@material-ui/core/IconButton';
import FileCopyIcon from '@material-ui/icons/FileCopy';

import { Translate, withLocalize } from 'react-localize-redux';
import siteTranslations from '../../translations/translate.json';
import planData from '../../data/plans';

import { setScreen } from '../../actions/app';

import { showSnackbar } from '../../actions/snackbar';

import customTheme from '../Common/customTheme';

import {
  sunToTrx,
  TRON_WEB_STATE_NOT_MAINNET,
  trxToSun,
} from '../../utils/tronCommon';

import tronMain from '../../utils/tronMain';

import {
  SNACKBAR_ERROR,
  SNACKBAR_INFO,
  SNACKBAR_WARNING,
} from '../Common/GameSnackbar';
import Tracker from '../../utils/tracker';
import { parseReferralAddress } from '../../utils/parseQueryString';

const CONTRACT_URL =
  'https://www.tronscan.org/#/contract/TWB81E1ShsaqLpbQsbKTgEy3LxQK8iBhXa/code';
const TRADE_URL = 'https://www.justswap.io/?lang=en-US#/scan/detail/trx/TV2EWPPhFj3q9KCuXjD6pHsLHvgNAmwLtF';

const styles = theme => ({
  button: {
    '&:hover': {
      backgroundColor: 'transparent',
    },
  },
});

const WEB_SITE_URL = 'https://trondex.org';

const gameStartTime = moment.utc('2020-10-05T12:00:00');
// Renderer callback with condition
const renderer = ({ days, hours, minutes, seconds, completed }) => {
  if (completed) {
    // Start the game
    // window.location.reload();
    return null;
  } else {
    // Render a countdown
    return (
      <Grid item xs={12}>
        <span className="medium-text"><Translate id="max.gamestart" /></span>
        <span className="xxl-text glowing-pink">{hours}</span>{' '}
        <span className="medium-text">
          <Translate id="max.hours" />{' '}
        </span>{' '}
        <span className="xxl-text glowing-pink">{minutes}</span>{' '}
        <span className="medium-text">
          <Translate id="max.minutes" />{' '}
        </span>{' '}
        <span className="xxl-text glowing-pink">{seconds}</span>{' '}
        <span className="medium-text">
          <Translate id="max.seconds" />
        </span>{' '}
      </Grid>
    );
  }
};

const initialState = {
  depositAmounts: 100,
  isLoading: false,
  accountValidForReferral: false,
  accountBalance: 0,
  contractBalance: 0,
  contractBalanceRate: 0,
  userPercentRate: 0,
  baseRate:10,
  playerCount: 0,
  totalInvestments: 0,
  playerAddress: null,
  availableReferrerEarnings: 0,
  level1RefCount: 0,
  level2RefCount: 0,
  level3RefCount: 0,
  userAvailable: 0,
  userTotalDeposits: 0,
  userTotalWithdrawn: 0,
  userCheckpoint: null,
  availableAirdrop: 0,
  withdrawnAirdrop: 0,
  airdropRate: 1000,
};

let loadDataInterval;
const INTERVAL = 5000; // 5 sec

const MINIMUM_DEPOSIT_GAS_FEE = trxToSun(3); // 3 TRX for gas fee
const MINIMUM_WITHDRAW_GAS_FEE = trxToSun(1); // 1 TRX for gas fee

class DailyDivs extends React.Component {
  constructor(props) {
    super(props);

    this.state = initialState;
    this.props.addTranslation(siteTranslations);
  }

  async componentDidMount() {
    const referralAddress = parseReferralAddress();
    this.setState({ referralAddress });

    const tronWebState = await tronMain.initTronWeb();
    if (tronWebState === TRON_WEB_STATE_NOT_MAINNET) {
      return;
    }

    await this.loadData();
    clearInterval(loadDataInterval);
    loadDataInterval = setInterval(async () => {
      try {
        await this.loadData();
      } catch (e) {
        console.error(e);
        clearInterval(loadDataInterval);
      }
    }, INTERVAL);

    Tracker.pageview('/max');
  }

  componentWillUnmount() {
    clearInterval(loadDataInterval);
  }

  async componentDidUpdate(prevProps) {
    if (this.props.reloadAccountNonce !== prevProps.reloadAccountNonce) {
      this.setState(initialState);
      await this.loadData();
    }
  }

  loadData = async () => {
    await this.loadPlayerData();
    await this.loadDivsData();
  };

  loadPlayerData = async () => {
    await this.getPlayerData();
  };

  loadDivsData = async () => {
    const playerAddress = tronMain.getCurrentAddress();

    const contractBalance = await tronMain.getBalance();
    const playerCount = await tronMain.getPlayerCount();

    const totalInvestments = await tronMain.getTotalInvestments();
    // const totalWithdraws = await tronMain.getTotalWithdraws();

    const userAvailable = await tronMain.getUserAvailable();
    const contractBalanceRate = await tronMain.getContractBalanceRate();
    const userPercentRate = await tronMain.getUserPercentRate();
    const userTotalDeposits = await tronMain.getUserTotalDeposits();
    const userTotalWithdrawn = await tronMain.getUserTotalWithdrawn();

    const availableReferrerEarnings = await tronMain.getUserReferralBonus();
    const userCheckpoint = await tronMain.getUserCheckpoint();
    
    const level1RefCount = await tronMain.getUserReferralCountByLevel(1);
    const level2RefCount = await tronMain.getUserReferralCountByLevel(2);
    const level3RefCount = await tronMain.getUserReferralCountByLevel(3);

    const availableAirdrop = await tronMain.getUserAvailableWithdrawAirdrop();
    const withdrawnAirdrop = await tronMain.getUserWithdrawnAirdrop();
    const airdropRate = await tronMain.getAirdropRate();

    this.setState({
      contractBalance,
      playerCount,
      playerAddress,
      totalInvestments,
      contractBalanceRate,
      userPercentRate,
      availableReferrerEarnings,
      level1RefCount,
      level2RefCount,
      level3RefCount,
      userAvailable,
      userTotalDeposits,
      userTotalWithdrawn,
      userCheckpoint,
      accountValidForReferral: userTotalDeposits > 0,
      availableAirdrop,
      withdrawnAirdrop,
      airdropRate,
    });
  };

  getPlayerData = async () => {
    const accountBalance = await tronMain.currentAddressBalance();
    this.setState({
      accountBalance,
    });
  };

  isDepositDisabled = () => {
    const { isLoading, withdrawableDivs, investmentDivs } = this.state;
    const gameStarted = gameStartTime.isBefore(new Date());
    return !!isLoading || (withdrawableDivs === 0 && investmentDivs > 0) || !gameStarted;
  };

  isWithdrawDisabled = () => {
    const { isLoading, withdrawableDivs } = this.state;
    const gameStarted = gameStartTime.isBefore(new Date());
    return !!isLoading || withdrawableDivs === 0 || !gameStarted;
  };

  isAirdropDisabled = () => {
    const { isLoading, availableAirdrop } = this.state;
    return !!isLoading || availableAirdrop === 0;
  };

  formatAmount = amount => {
    const formattedAmount = amount
      .replace(/^0+(?!\.|$)/, '')
      .replace(/[^0-9 .]+/g, '')
      .replace(/(\d)(?=(\d\d\d)+(?!\d))/g, '$1');
    if (formattedAmount.trim() === '') {
      return '';
    } else {
      return formattedAmount;
    }
  };

  getPlanInfo = planId => {
    const plan = planData[planId];
    return plan;
  };

  copyLink = () => {
    const { showSnackbar } = this.props;
    const { myReferralLink } = this.state;

    showSnackbar('message.referralLinkCopied');
    Tracker.event({
      category: 'Max',
      action: 'Copy Link',
      label: myReferralLink,
    });
  };

  handleDepositAmountChange = async event => {
    const formattedAmount = this.formatAmount(event.target.value);
    let { depositAmounts } = this.state;

    depositAmounts = formattedAmount;
    this.setState({ depositAmounts: depositAmounts });
  };

  deposit = async planId => {
    const { referralAddress, accountBalance } = this.state;
    let { depositAmounts } = this.state;
    const { showSnackbar } = this.props;
    Tracker.event({
      category: 'Max',
      action: 'Deposit Fund',
    });

    if (depositAmounts < 100) {
      showSnackbar('message.depositNumberMustBePositive', SNACKBAR_WARNING);
      return;
    }

    if (
      depositAmounts >
      sunToTrx(accountBalance - MINIMUM_DEPOSIT_GAS_FEE)
    ) {
      // Set the `depositAmount`, otherwise, can't deposit
      depositAmounts = sunToTrx(
        accountBalance - MINIMUM_DEPOSIT_GAS_FEE
      );
    }

    if (depositAmounts <= 0) {
      showSnackbar('message.depositNeedsGasFee', SNACKBAR_WARNING);
      return;
    }

    try {
      const result = await tronMain.invest(
        depositAmounts,
        referralAddress,
        planId
      );
      if (!result) {
        return;
      }
      showSnackbar('message.depositSucceed', SNACKBAR_INFO);
      await this.loadData();
    } catch (e) {
      console.error(e);
      if (e === 'Confirmation declined by user') {
        showSnackbar('message.cancelAction', SNACKBAR_INFO);
      } else {
        if (e.error) {
          if (e.error === 'CONTRACT_VALIDATE_ERROR') {
            if (e.message.indexOf('balance is not sufficient') > 0) {
              showSnackbar('message.notEnoughFund', SNACKBAR_ERROR);
            }
          } else if (e.error === 'Cannot find result in solidity node') {
            // showSnackbar('message.timout', SNACKBAR_ERROR);
            showSnackbar('message.depositSucceed', SNACKBAR_INFO);
          } else {
            showSnackbar(e.error, SNACKBAR_ERROR);
          }
        }
      }
    }
  };

  withdraw = async () => {
    const { showSnackbar } = this.props;
    const { withdrawableDivs, accountBalance } = this.state;
    Tracker.event({
      category: 'Max',
      action: 'Withdraw Fund',
    });

    if (withdrawableDivs <= 0) {
      showSnackbar(
        'message.withdrawableNumberMustBePositive',
        SNACKBAR_WARNING
      );
      return;
    }

    if (accountBalance < MINIMUM_WITHDRAW_GAS_FEE) {
      showSnackbar('message.withdrawNeedsGasFee', SNACKBAR_WARNING);
      return;
    }

    try {
      const result = await tronMain.withdrawDivs();
      if (!result) {
        return;
      }
      showSnackbar('message.withdrawSucceed', SNACKBAR_INFO);
      await this.loadData();
    } catch (e) {
      console.error(e);
      if (e === 'Confirmation declined by user') {
        showSnackbar('message.cancelAction', SNACKBAR_INFO);
      } else {
        if (e.error) {
          if (e.error === 'CONTRACT_VALIDATE_ERROR') {
            if (e.message.indexOf('balance is not sufficient') > 0) {
              showSnackbar('message.notEnoughFund', SNACKBAR_ERROR);
            }
          } else if (e.error === 'Cannot find result in solidity node') {
            showSnackbar('message.withdrawSucceed', SNACKBAR_INFO);
          } else {
            showSnackbar(e.error, SNACKBAR_ERROR);
          }
        }
      }
    }
  };

  getAirdrop = async () => {
    const { showSnackbar } = this.props;
    const { accountBalance } = this.state;
    Tracker.event({
      category: 'Max',
      action: 'Get Airdrop',
    });

    if (accountBalance < MINIMUM_WITHDRAW_GAS_FEE) {
      showSnackbar('message.getAirdropNeedsGasFee', SNACKBAR_WARNING);
      return;
    }

    try {
      const result = await tronMain.getAirdrop();
      if (!result) {
        return;
      }
      showSnackbar('message.getAirdropSucceed', SNACKBAR_INFO);
      await this.loadData();
    } catch (e) {
      console.error(e);
      if (e === 'Confirmation declined by user') {
        showSnackbar('message.cancelAction', SNACKBAR_INFO);
      } else {
        if (e.error) {
          if (e.error === 'CONTRACT_VALIDATE_ERROR') {
            if (e.message.indexOf('balance is not sufficient') > 0) {
              showSnackbar('message.notEnoughFund', SNACKBAR_ERROR);
            }
          } else if (e.error === 'Cannot find result in solidity node') {
            showSnackbar('message.getAirdropSucceed', SNACKBAR_INFO);
          } else {
            showSnackbar(e.error, SNACKBAR_ERROR);
          }
        }
      }
    }
  };
  
  renderCountdownIfNeeded() {
    // const { loaded } = this.state;
    return (
      <Countdown date={gameStartTime.toDate()} renderer={renderer} />
    );
  }

  render() {
    const {
      depositAmounts,
      accountValidForReferral,
      isLoading,
      playerAddress,
      contractBalance,
      playerCount,
      totalInvestments,
      availableReferrerEarnings,
      level1RefCount,
      level2RefCount,
      level3RefCount,
      userAvailable,
      accountBalance,
      userTotalDeposits,
      userTotalWithdrawn,
      userCheckpoint,
      baseRate,
      userPercentRate,
      contractBalanceRate,
      availableAirdrop,
      withdrawnAirdrop,
      airdropRate,
    } = this.state;

    const { classes } = this.props;
    const myReferralLink =
      window.tronWeb && playerAddress ? `${WEB_SITE_URL}?ref=${playerAddress}` : ``;
    const processWithdrawText = isLoading
      ? 'max.processing'
      : 'max.withdrawtowallet';

    const userPercentRateDisplay = userPercentRate/10;
    const baseRateDisplay = baseRate/10;
    const userHoldOutRateDisplay = (userPercentRate - contractBalanceRate)/10;
    const contractBalanceRateDisplay = contractBalanceRate-baseRate >=0? (contractBalanceRate-baseRate)/10:0;

    return (
      <MuiThemeProvider theme={customTheme}>
        <div className="cards">
          <div color="primary" className="card home-card">
            <div className="container">
              <div className="space" />
              <Grid container spacing={24} className="component-card">
               <Grid item xs={12}>
                  <div className="announcement">
                    <p className="announcement-text">
                      <Translate id="max.announcement" />
                    </p>
                  </div>
                </Grid>
                <Grid item xs={12}>
                  <div className="jumbotron">
                    <Grid container spacing={24}>
                      <Grid item xs={12}>
                        <div className="xxl-size white">
                          <Translate id="max.maintitle" />
                        </div>
                        <div className="xxl-size aqua">
                          <Translate id="max.subtitle1" />
                        </div>

                        <div className="space" />

                        <div className="x-m-size gold">
                          <Translate id="max.subtitle2" />
                        </div>

                        <div className="x-m-size gold">
                          <Translate id="max.subtitle3" />
                        </div>
                      </Grid>
                      <Grid item xs={12}>
                        <Button
                          className="gta-button"
                          variant="contained"
                          disableRipple={true}
                          href={CONTRACT_URL}
                          target="_blank"
                        >
                          <Translate id="max.verify" />{' '}
                          <Translate id="max.contract" />
                        </Button>
                        <Button
                          className="gta-invest-button"
                          variant="contained"
                          disableRipple={true}
                          href="#section_invest"
                        >
                          <Translate id="max.investment" />{' '}
                        </Button>
                        <Button
                          className="gta-invest-button"
                          variant="contained"
                          disableRipple={true}
                          href={TRADE_URL}
                          target="_blank"
                        >
                          <Translate id="max.tradetdx" />
                        </Button>
                        <Button
                          className="contract-button"
                          variant="contained"
                          disableRipple={true}
                          href="#section_airdrop"
                        >
                          <Translate id="max.claimairdrop" />
                        </Button>
                      </Grid>
                    </Grid>
                  </div>
                </Grid>
                {this.renderCountdownIfNeeded()}
                <Grid item xs={12}>
                  <div className="m-size grey">
                    <img
                      src="images/misc/line.svg"
                      className="image-small"
                      alt="tron"
                    />
                  </div>
                </Grid>
                <div id="section_stats" />
                <Grid item xs={12}>
                  <div className="">
                    <div className="jumbotron">
                      <Grid container spacing={24}>
                        <Grid item xs={12} md={4}>
                          <div>
                            <img
                              src="images/misc/balance.svg"
                              className="image-medium"
                              alt="tron"
                            />
                          </div>
                          <div className="global-stats-item s-size grey">
                            <Translate id="max.contractbalance" />
                          </div>
                          <div className="global-stats-item l-size glowing-pink">
                            {contractBalance.toLocaleString()}
                          </div>
                          <div className="global-stats-item s-size grey">
                            TRX
                          </div>
                        </Grid>
                        <Grid item xs={12} md={4}>
                          <div>
                            <img
                              src="images/misc/users.svg"
                              className="image-medium"
                              alt="tron"
                            />
                          </div>
                          <div className="global-stats-item s-size grey">
                            <Translate id="max.users" />
                          </div>
                          <div className="global-stats-item l-size glowing-pink">
                            {playerCount}
                          </div>
                          <div className="global-stats-item s-size grey">
                            <Translate id="max.count_users" />
                          </div>
                        </Grid>
                        <Grid item xs={12} md={4}>
                          <div>
                            <img
                              src="images/misc/investment.svg"
                              className="image-medium"
                              alt="tron"
                            />
                          </div>
                          <div className="global-stats-item s-size grey">
                            <Translate id="max.totalinvestments" />
                          </div>
                          <div className="global-stats-item l-size glowing-pink">
                            {totalInvestments.toLocaleString()}
                          </div>
                          <div className="global-stats-item s-size grey">
                            TRX
                          </div>
                        </Grid>
                        
                      </Grid>
                    </div>
                  </div>
                </Grid>

                <Grid item xs={12}>
                  <div className="m-size grey">
                    <img
                      src="images/misc/line.svg"
                      className="image-small"
                      alt="tron"
                    />
                  </div>
                </Grid>

                <div id="section_invest" />
                <Grid item xs={12} md={6}>
                  <div className="card">
                    <div className="dd-card">
                      <Grid container spacing={0}>
                        <Grid item xs={12}>
                          <div className="buy-placeholder grey-border deposit-card deposit-withdraw-card">
                            <div className="my-account middle-children">
                              <img
                                src='images/misc/diamond.svg'
                                className="image-small"
                                alt="tron"
                              />
                              <span className="m-size dark plan-name">
                                <Translate id="max.diamond" />
                              </span>
                            </div>
                            <div className="space" />
                            <div className="input-box">
                              <TextField
                                label=""
                                className="buy-token"
                                autoComplete="off"
                                value={depositAmounts}
                                onChange={this.handleDepositAmountChange}
                                InputProps={{
                                  endAdornment: (
                                    <img
                                      src="images/misc/logo.svg"
                                      className="logo"
                                      alt="tron"
                                    />
                                  ),
                                }}
                                margin="normal"
                                variant="outlined"
                              />
                            </div>
                            <Grid container spacing={24}>
                              <Grid item xs={12}>
                                <Button
                                  id="invest-button"
                                  className="invest-button"
                                  variant="contained"
                                  disableRipple={true}
                                  disabled={this.isDepositDisabled()}
                                  onClick={() =>
                                    this.deposit()
                                  }
                                >
                                  <Translate id="max.invest" />
                                </Button>
                                <div className="s-size grey">
                                  <Translate id="max.minwarning" />
                                </div>
                              </Grid>
                            </Grid>
                          </div>
                        </Grid>
                      </Grid>
                    </div>
                  </div>
                </Grid>
                
                <div id="section_myaccount" />
                <Grid item xs={12} md={6}>
                  <div className="card">
                    <div className="dd-card">
                    <div className="buy-placeholder grey-border withdraw-card deposit-withdraw-card">
                    <Grid container spacing={24}>
                    <div className="space" />
                      <Grid item xs={12} md={6}>
                        <Grid container spacing={24}>
                          <Grid item xs={12} md={12}>
                            <div className="middle-children">
                              <img
                                src="images/misc/investment.svg"
                                className="image-small"
                                alt="tron"
                              />
                              <span className="my-account-title">
                                <Translate id="max.investment" />
                              </span>
                            </div>
                            <div className="x-m-size middle-children">
                              <div className="blue balance-number">
                                {userTotalDeposits || 0} TRX
                              </div>
                            </div>
                          </Grid>
                          <Grid item xs={12} md={12}>
                            <div className="middle-children">
                              <img
                                src="images/misc/wallet.svg"
                                className="image-small"
                                alt="tron"
                              />
                              <span className="my-account-title">
                                <Translate id="max.payout" />
                              </span>
                            </div>
                            <div className="x-m-size middle-children">
                              <div className="blue balance-number">
                                {userTotalWithdrawn || 0} TRX
                              </div>
                            </div>
                          </Grid>
                          <Grid item xs={12} md={12}>
                            <div className="middle-children">
                              <img
                                src="images/misc/withdraw.svg"
                                className="image-small"
                                alt="tron"
                              />
                              <span className="my-account-title">
                                <Translate id="max.withdrawableTrx" />
                              </span>
                            </div>
                            <div className="x-m-size middle-children">
                              <div className="green balance-number">
                                {userAvailable || 0} TRX
                              </div>
                            </div>
                          </Grid>
                        </Grid>
                      </Grid>
                      <Grid item xs={12} md={6}>
                        <Grid container spacing={0}>
                          <Grid item xs={12} md={12}>
                            <div className="middle-children">
                              <img
                                src="images/misc/referral.svg"
                                className="image-small"
                                alt="tron"
                              />
                              <span className="my-account-title">
                                <Translate id="max.userPercentRate" />
                              </span>
                            </div>
                            <div>
                              <div className="blue balance-number">{' '}
                                {userPercentRateDisplay || 0}%
                              </div>
                            </div>
                          </Grid>
                          <Grid item xs={12}>
                            <div className="global-stats-item s-size blue">
                              <Translate id="max.baseRate" />{' '}
                               {baseRateDisplay || 0}{' '}%
                            </div>
                            
                            <div className="global-stats-item s-size blue">
                              <Translate id="max.contractbonus" />{' '}
                              {contractBalanceRateDisplay || 0}%
                            </div>

                            <div className="global-stats-item s-size blue">
                              <Translate id="max.holdbonus" />{' '}
                               {userHoldOutRateDisplay || 0}%
                            </div>

                            <div className="global-stats-item s-size gold">
                              <Translate id="max.userCheckpoint" />{' '}
                               {userCheckpoint}
                            </div>
                          </Grid>
                        </Grid>
                      </Grid>
                      <Grid item xs={12}>
                          <Button
                            id="sell-button"
                            variant="contained"
                            className="withdraw-button"
                            disableRipple={true}
                            disabled={this.isWithdrawDisabled()}
                            onClick={this.withdraw}
                          >
                            <span className="my-account-title">
                              <Translate id={processWithdrawText} />
                            </span>
                          </Button>
                          <div className="s-size grey">
                            <Translate id="max.feewarning" />
                          </div>
                       </Grid>
                    </Grid>
                    </div>
                    </div>
                  </div>
                </Grid>

                <div id="section_airdrop" />
                <Grid item xs={12} md={12}>
                  <div className="card">
                    <div className="space" />
                    <div className="dd-card buy-placeholder grey-border">
                    <Grid container spacing={24}>
                      <Grid item xs={12} md={12}>
                        <Grid container spacing={0}>
                          <Grid item xs={12} md={6}>
                            <div className="space" />
                            <div className="middle-children">
                              <img
                                src="images/misc/gold.svg"
                                className="image-small"
                                alt="tron"
                              />
                              <span className="my-account-title">
                                <Translate id="max.availableAirdrop" />
                              </span>
                            </div>
                            <div>
                              <div className="green balance-number">
                                {availableAirdrop || 0} TDX
                              </div>
                            </div>

                            <div className="middle-children">
                              <img
                                src="images/misc/platinum.svg"
                                className="image-small"
                                alt="tron"
                              />
                              <span className="my-account-title">
                                <Translate id="max.withdrawnAirdrop" />
                              </span>
                            </div>
                            <div>
                              <div className="balance-number">
                                {withdrawnAirdrop || 0} TDX
                              </div>
                            </div>
                            
                            <div className="s-size">
                              <Translate id="max.tdxMessage1" /> 
                               {' '}{airdropRate}{' '}
                              <Translate id="max.tdxMessage2" />
                            </div>
                            <div className="space" />
                          </Grid>
                          <Grid item xs={12} md={6}>
                            <Button
                              id="sell-button"
                              variant="contained"
                              className="invest-button"
                              disableRipple={true}
                              disabled={this.isAirdropDisabled()}
                              onClick={this.getAirdrop}
                            >
                              <span className="my-account-title">
                                <Translate id="max.getairdrop" />
                              </span>
                            </Button>
                            <div className="s-size grey">
                              <Translate id="max.feewarningairdrop" />
                            </div>
                            <div className="space" />
                          </Grid>
                        </Grid>
                      </Grid>
                    </Grid>
                    </div>
                  </div>
                </Grid>

                <Grid item xs={12} md={12}>
                  <div className="card">
                    <div className="space" />
                    <div className="dd-card buy-placeholder grey-border">
                    <Grid container spacing={24}>
                      <Grid item xs={12} md={12}>
                        <Grid container spacing={0}>
                          {accountValidForReferral ? (
                            <Grid item xs={12} md={6}>
                              <div className="middle-children">
                                <img
                                  src="images/misc/referral.svg"
                                  className="image-small"
                                  alt="tron"
                                />
                                <span className="my-account-title">
                                  <Translate id="max.availableReferralEarnings" />
                                </span>
                              </div>
                              <div>
                                <div className="green balance-number">
                                  {availableReferrerEarnings || 0} TRX
                                </div>
                              </div>
                              <div className="input-box">
                                <TextField
                                  className="divs-referral-link-box"
                                  value={myReferralLink}
                                  margin="normal"
                                  color="secondary"
                                  variant="outlined"
                                />
                              </div>
                              <div className="referral-message">
                                <span className="s-size">
                                  <Translate id="max.yourreferrallink" />
                                  <CopyToClipboard
                                    text={myReferralLink}
                                    disableRipple={true}
                                    onCopy={() => {
                                      this.copyLink();
                                    }}
                                  >
                                    <IconButton className={classes.button}>
                                      <FileCopyIcon />
                                    </IconButton>
                                  </CopyToClipboard>
                                </span>
                              </div>
                            </Grid>
                          ) : accountBalance === 0 ? (
                            <Grid item xs={12} md={6}>
                              <div className="space" />
                              <div className="middle-children">
                                <img
                                  src="images/misc/referral.svg"
                                  className="image-small"
                                  alt="tron"
                                />
                                <span className="x-m-size">
                                  <Translate id="max.needPositiveBalance" />
                                </span>
                              </div>
                              <div className="space" />
                            </Grid>
                          ) : (
                            <Grid item xs={12} md={6}>
                              <div className="space" />
                              <div className="middle-children">
                                <img
                                  src="images/misc/referral.svg"
                                  className="image-small"
                                  alt="tron"
                                />
                                <span className="x-m-size">
                                  <Translate id="max.invalidreferral" />
                                </span>
                              </div>
                              <div className="space" />
                            </Grid>
                          )}
                          <Grid item xs={12} md={6}>
                            <div className="global-stats-item x-m-size blue">
                              <Translate id="max.1stlvlreferral" />
                              5%): {level1RefCount}
                            </div>
                            <div className="global-stats-item x-m-size blue">
                              <Translate id="max.2ndlvlreferral" />
                              2%): {level2RefCount}
                            </div>
                            <div className="global-stats-item x-m-size blue">
                              <Translate id="max.3rdlvlreferral" />
                              0.5%): {level3RefCount}
                            </div>
                          </Grid>
                        </Grid>
                      </Grid>
                    </Grid>
                    </div>
                  </div>
                </Grid>
                <div id="section_faq" />
                <Grid item xs={12}>
                  <div className="dark-bg faq-card">
                    <div className="my-account dark-bg">
                      <span className="m-size aqua">
                        <Translate id="max.faq" />
                      </span>
                    </div>
                    <div className="content-card">
                      <div className="dark-bg intro-placeholder">
                        <ul>
                          <li className="m-size glowing-blue">
                            <Translate id="faq.q1" />
                          </li>
                          <li className="s-size grey">
                            <Translate id="faq.a1" />
                          </li>

                          <li className="m-size glowing-blue">
                            <Translate id="faq.q2" />
                          </li>
                          <li className="s-size grey">
                            <Translate id="faq.a2" />
                          </li>

                          <li className="m-size glowing-blue">
                            <Translate id="faq.q3" />
                          </li>
                          <li className="s-size grey">
                            <Translate id="faq.a3" />
                          </li>

                          <li className="m-size glowing-blue">
                            <Translate id="faq.q4" />
                          </li>
                          <li className="s-size grey">
                            <Translate id="faq.a4" />
                          </li>
                          <li className="m-size glowing-blue">
                            <Translate id="faq.q5" />
                          </li>
                          <li className="s-size grey">
                            <Translate id="faq.a5" />
                          </li>
                          <li className="m-size glowing-blue">
                            <Translate id="faq.q6" />
                          </li>
                          <li className="s-size grey">
                            <Translate id="faq.a6" />
                          </li>
                          <li className="s-size glowing-pink">
                            <Translate id="max.disclaimer" />
                          </li>
                        </ul>
                      </div>
                    </div>
                  </div>
                </Grid>
              </Grid>
            </div>
          </div>
          <div className="space" />
        </div>
      </MuiThemeProvider>
    );
  }
}

const mapStateToProps = state => ({
  reloadAccountNonce: state.app.reloadAccountNonce,
});

const mapDispatchToProps = {
  showSnackbar,
  setScreen,
};

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(withLocalize(withStyles(styles)(DailyDivs)));
