import React from 'react';
import { connect } from 'react-redux';
import Countdown from 'react-countdown-now';
import moment from 'moment';
import { MuiThemeProvider } from '@material-ui/core/styles';
import { withStyles } from '@material-ui/core/styles';
import Grid from '@material-ui/core/Grid';
import Button from '@material-ui/core/Button';

import { Translate, withLocalize } from 'react-localize-redux';
import siteTranslations from '../../translations/translate.json';
import { SCREEN_VAULT, SCREEN_VAULT_SUPER,SCREEN_VAULT_LIGHTNING, setScreen } from '../../actions/app';

import { showSnackbar } from '../../actions/snackbar';

import customTheme from '../Common/customTheme';

import {
  sunToTrx,
  TRON_WEB_STATE_NOT_MAINNET,
  trxToSun,
} from '../../utils/tronCommon';

import tronMain from '../../utils/tronMainSuper';

import Tracker from '../../utils/tracker';
import { parseReferralAddress } from '../../utils/parseQueryString';

const CONTRACT_URL =
  'https://www.tronscan.org/#/contract/TWB81E1ShsaqLpbQsbKTgEy3LxQK8iBhXa/code';
const TRADE_URL = 'https://www.justswap.io/?lang=en-US#/scan/detail/trx/TV2EWPPhFj3q9KCuXjD6pHsLHvgNAmwLtF';

const styles = theme => ({
  button: {
    '&:hover': {
      backgroundColor: 'transparent',
    },
  },
});

const WEB_SITE_URL = 'https://trondex.org';

const gameStartTime = moment.utc('2020-10-30T11:00:00');
// Renderer callback with condition
const renderer = ({ days, hours, minutes, seconds, completed }) => {
  if (completed) {
    // Start the game
    return null;
  } else {
    // Render a countdown
    return (
      <Grid item xs={12}>
        <span className="xxl-text glowing-pink">{days}</span>{' '}
        <span className="medium-text">
          <Translate id="max.days" />{' '}
        </span>{' '}
        <span className="xxl-text glowing-pink">{hours}</span>{' '}
        <span className="medium-text">
          <Translate id="max.hours" />{' '}
        </span>{' '}
        <span className="xxl-text glowing-pink">{minutes}</span>{' '}
        <span className="medium-text">
          <Translate id="max.minutes" />{' '}
        </span>{' '}
        <span className="xxl-text glowing-pink">{seconds}</span>{' '}
        <span className="medium-text">
          <Translate id="max.seconds" />
        </span>{' '}
      </Grid>
    );
  }
};

const initialState = {
  depositAmounts: 100,
  isLoading: false,
  accountValidForReferral: false,
  accountBalance: 0,
  contractBalance: 0,
  contractBalanceRate: 0,
  userPercentRate: 0,
  baseRate:10,
  playerCount: 0,
  totalInvestments: 0,
  playerAddress: null,
  availableReferrerEarnings: 0,
  level1RefCount: 0,
  level2RefCount: 0,
  level3RefCount: 0,
  userAvailable: 0,
  userTotalDeposits: 0,
  userTotalWithdrawn: 0,
  userCheckpoint: null,
  availableAirdrop: 0,
  withdrawnAirdrop: 0,
  airdropRate: 1000,
};

let loadDataInterval;
const INTERVAL = 5000; // 5 sec

const MINIMUM_DEPOSIT_GAS_FEE = trxToSun(3); // 3 TRX for gas fee
const MINIMUM_WITHDRAW_GAS_FEE = trxToSun(1); // 1 TRX for gas fee

class Landing extends React.Component {
  constructor(props) {
    super(props);

    this.state = initialState;
    this.props.addTranslation(siteTranslations);
  }

  async componentDidMount() {
    const referralAddress = parseReferralAddress();
    this.setState({ referralAddress });

    const tronWebState = await tronMain.initTronWeb();
    if (tronWebState === TRON_WEB_STATE_NOT_MAINNET) {
      return;
    }

    clearInterval(loadDataInterval);
    loadDataInterval = setInterval(async () => {
      try {
        await this.loadData();
      } catch (e) {
        console.error(e);
        clearInterval(loadDataInterval);
      }
    }, INTERVAL);

    Tracker.pageview('/max');
  }

  componentWillUnmount() {
    clearInterval(loadDataInterval);
  }

  async componentDidUpdate(prevProps) {
    if (this.props.reloadAccountNonce !== prevProps.reloadAccountNonce) {
      this.setState(initialState);
      await this.loadData();
    }
  }

  navigateTo = screen => {
    const { setScreen } = this.props;
    this.setState({ mobileOpen: false });
    setScreen(screen);
  };

  renderCountdownIfNeeded() {
    // const { loaded } = this.state;
    return (
      <Countdown date={gameStartTime.toDate()} renderer={renderer} />
    );
  }

  render() {
    
    return (
      <MuiThemeProvider theme={customTheme}>
        <div className="cards">
          <div color="primary" className="card home-card">
            <div className="container">
              <div className="space" />
              <Grid container spacing={24} className="component-card">
                <Grid item xs={12}>
                  <div className="jumbotron">
                    <Grid container spacing={24}>

                      <Grid item xs={12}>
                        <div className="xxl-size white">
                          <Translate id="max.maintitlelightning" />
                        </div>
                        <div className="xxl-size aqua">
                          <Translate id="max.subtitle1lightning" />
                        </div>
                      </Grid>
                    </Grid>
                  </div>
                </Grid>
                <Grid item xs={12}>
                  <div className="m-size grey">
                    <img
                      src="images/misc/line.svg"
                      className="image-small"
                      alt="tron"
                    />
                  </div>
                </Grid>
                {this.renderCountdownIfNeeded()}
                <div id="section_invest" />
                <Grid item xs={12} md={4}>
                  <div className="card">
                    <div className="dd-card">
                      <Grid container spacing={0}>
                        <Grid item xs={12}>
                          <div className="buy-placeholder grey-border deposit-withdraw-card">
                            <div className="my-account middle-children">
                              <img
                                src='images/misc/platinum.svg'
                                className="image-small"
                                alt="tron"
                              />
                              <span className="m-size dark plan-name">
                                <Translate id="max.lightningroi" />
                              </span>
                            </div>
                            <div className="space" />
                            <Grid container spacing={24}>
                              <Grid item xs={12}>
                                <Button
                                  id="invest-button"
                                  className="invest-button"
                                  variant="contained"
                                  disableRipple={true}
                                  onClick={() =>
                                    this.navigateTo(SCREEN_VAULT_LIGHTNING)
                                  }
                                >
                                  <Translate id="max.enter" />
                                </Button>
                              </Grid>
                            </Grid>
                          </div>
                        </Grid>
                      </Grid>
                    </div>
                  </div>
                </Grid>
                
                
                <Grid item xs={12} md={4}>
                  <div className="card">
                    <div className="dd-card">
                      <Grid container spacing={0}>
                        <Grid item xs={12}>
                          <div className="buy-placeholder grey-border deposit-withdraw-card">
                            <div className="my-account middle-children">
                              <img
                                src='images/misc/diamond.svg'
                                className="image-small"
                                alt="tron"
                              />
                              <span className="m-size dark plan-name">
                                <Translate id="max.regularroi" />
                              </span>
                            </div>
                            <div className="space" />
                            <Grid container spacing={24}>
                              <Grid item xs={12}>
                                <Button
                                  id="invest-button"
                                  className="invest-button"
                                  variant="contained"
                                  disableRipple={true}
                                  onClick={() =>
                                    this.navigateTo(SCREEN_VAULT)
                                  }
                                >
                                  <Translate id="max.enter" />
                                </Button>
                              </Grid>
                            </Grid>
                          </div>
                        </Grid>
                      </Grid>
                    </div>
                  </div>
                </Grid>
                <Grid item xs={12} md={4}>
                  <div className="card">
                    <div className="dd-card">
                      <Grid container spacing={0}>
                        <Grid item xs={12}>
                          <div className="buy-placeholder grey-border deposit-withdraw-card">
                            <div className="my-account middle-children">
                              <img
                                src='images/misc/gold.svg'
                                className="image-small"
                                alt="tron"
                              />
                              <span className="m-size dark plan-name">
                                <Translate id="max.superroi" />
                              </span>
                            </div>
                            <div className="space" />
                            <Grid container spacing={24}>
                              <Grid item xs={12}>
                                <Button
                                  id="invest-button"
                                  className="invest-button"
                                  variant="contained"
                                  disableRipple={true}
                                  onClick={() =>
                                    this.navigateTo(SCREEN_VAULT_SUPER)
                                  }
                                >
                                  <Translate id="max.enter" />
                                </Button>
                              </Grid>
                            </Grid>
                          </div>
                        </Grid>
                      </Grid>
                    </div>
                  </div>
                </Grid>

              </Grid>
            </div>
          </div>
          <div className="space" />
        </div>
      </MuiThemeProvider>
    );
  }
}

const mapStateToProps = state => ({
  reloadAccountNonce: state.app.reloadAccountNonce,
});

const mapDispatchToProps = {
  showSnackbar,
  setScreen,
};

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(withLocalize(withStyles(styles)(Landing)));
