import React from 'react';
import { connect } from 'react-redux';

import { MuiThemeProvider } from '@material-ui/core/styles';
import Grid from '@material-ui/core/Grid';
import Button from '@material-ui/core/Button';
import TextField from '@material-ui/core/TextField';
import Card from '@material-ui/core/Card';

import { withLocalize, Translate } from 'react-localize-redux';
import siteTranslations from '../../translations/translate.json';

import { showSnackbar } from '../../actions/snackbar';

import customTheme from '../Common/customTheme';

import { TRON_WEB_STATE_NOT_MAINNET } from '../../utils/tronCommon';

import TronAccountDetails from '../../utils/tronAccountDetails';

import Tracker from '../../utils/tracker';
import { SNACKBAR_ERROR } from '../Common/GameSnackbar';

const initialState = {
  myTokens: null,
  myDividends: null,
  myRoundBuy: null,
  myRoundSell: null,
  myReferrals: null,
  accountAddress: '',
};

class AccountDetails extends React.Component {
  constructor(props) {
    super(props);

    this.state = initialState;
    this.props.addTranslation(siteTranslations);
  }

  async componentDidMount() {
    Tracker.pageview('/accountDetails');
  }

  search = async () => {
    const { showSnackbar } = this.props;
    const { accountAddress } = this.state;
    if (!accountAddress) {
      showSnackbar('Address can not be empty!', SNACKBAR_ERROR);
      return;
    }
    const tronWebState = await TronAccountDetails.initTronWeb(accountAddress);
    if (tronWebState === TRON_WEB_STATE_NOT_MAINNET) {
      return;
    }

    await this.loadData(accountAddress);
  };

  loadData = async accountAddress => {
    await this.getPlayerData(accountAddress);
  };

  getPlayerData = async address => {
    const playerData = await TronAccountDetails.getPlayerData(address);
    if (!playerData) {
      return;
    }
    const myTokens = playerData.myTokens;
    const myReferrals = playerData.myReferrals;
    const myDividends = playerData.myDividends;
    const myRoundBuy = playerData.myRoundBuy;
    const myRoundSell = playerData.myRoundSell;
    this.setState({
      myTokens,
      myDividends,
      myReferrals,
      myRoundBuy,
      myRoundSell,
    });
  };

  handleChange = async event => {
    this.setState({ [event.target.name]: event.target.value });
  };

  render() {
    const {
      myTokens,
      myDividends,
      myRoundBuy,
      myRoundSell,
      myReferrals,
      accountAddress,
    } = this.state;

    return (
      <MuiThemeProvider theme={customTheme}>
        <div className="cards">
          <Card color="primary" className="card">
            <Grid
              container
              spacing={24}
              className="component-card account-card"
            >
              <Grid item xs={12} md={6}>
                <div className="buy-placeholder">
                  <Grid item xs={12}>
                    <div className="buy-placeholder">
                      <div>
                        <span className="l-size">
                          <Translate id="account.searchAddress" />
                        </span>
                      </div>
                      <div className="space" />
                      <div className="input-box">
                        <TextField
                          label=""
                          name="accountAddress"
                          className="buy-token"
                          autoComplete="off"
                          placeholder="address e.g. T*****"
                          value={accountAddress}
                          onChange={this.handleChange}
                          margin="normal"
                          variant="outlined"
                        />
                      </div>

                      <Grid container spacing={24}>
                        <Grid item xs={12}>
                          <Button
                            id="buy-button"
                            className="buy-button"
                            variant="contained"
                            onClick={this.search}
                          >
                            <Translate id="account.search" />
                          </Button>
                        </Grid>
                      </Grid>
                    </div>
                  </Grid>
                  <Grid container spacing={24}>
                    <Grid item xs={12}>
                      <div className="space" />
                      <div className="s-size">
                        <Translate id="general.token" /> {myTokens || 0}{' '}
                        <span className="blue">
                          <Translate id="general.token" />
                        </span>
                      </div>
                      <div className="space" />
                      <div className="s-size">
                        <Translate id="general.myReferrals" />{' '}
                        {myReferrals || 0} <span className="blue">TRX</span>
                      </div>
                      <div className="space" />
                      <div className="s-size">
                        <Translate id="general.claimableTrx" />{' '}
                        {myDividends || 0} <span className="blue">TRX</span>
                      </div>
                    </Grid>
                    <Grid item xs={12}>
                      <div className="space" />
                      <div className="s-size">
                        <Translate id="general.roundBuyTrx" /> {myRoundBuy || 0}{' '}
                        <span className="blue">TRX</span>
                      </div>
                      <div className="space" />
                      <div className="s-size">
                        <Translate id="general.roundSellTrx" />{' '}
                        {myRoundSell || 0} <span className="blue">TRX</span>
                      </div>
                    </Grid>
                  </Grid>
                </div>
              </Grid>
            </Grid>
          </Card>
        </div>
      </MuiThemeProvider>
    );
  }
}

const mapDispatchToProps = {
  showSnackbar,
};

export default connect(
  null,
  mapDispatchToProps
)(withLocalize(AccountDetails));
