export const TRON_WEB_STATE_NOT_INSTALLED = 'TRON_WEB_STATE_NOT_INSTALLED';
export const TRON_WEB_STATE_LOCKED = 'TRON_WEB_STATE_LOCKED';
export const TRON_WEB_STATE_UNLOCKED = 'TRON_WEB_STATE_UNLOCKED';
export const TRON_WEB_STATE_NOT_MAINNET = 'TRON_WEB_STATE_NOT_MAINNET';

export const FOUNDATION_ADDRESS = 'TLsV52sRDL79HXGGm9yzwKibb6BeruhUzy';
export const FOUNDATION_CODE = 6666;

export const injectTronWeb = async defaultTronWeb => {
  return new Promise(resolve => {
    let tronWebState = TRON_WEB_STATE_NOT_INSTALLED;

    // Try to inject it
    let tries = 0;
    const timer = setInterval(() => {
      if (tries >= 10) {
        if (!window.tronWeb) {
          window.tronWeb = defaultTronWeb;
        }
        clearInterval(timer);
        return resolve(tronWebState);
      }

      if (!!window.tronWeb && window.tronWeb !== defaultTronWeb) {
        tronWebState = TRON_WEB_STATE_LOCKED;

        if (window.tronWeb.ready) {
          tronWebState = TRON_WEB_STATE_UNLOCKED;

          // Already have it
          clearInterval(timer);
          return resolve(tronWebState);
        }
      }

      tries++;
    }, 100);
  });
};

export const initContract = async (
  tronWebState,
  defaultTronWeb,
  contractAddress
) => {
  let contract;
  try {
    if (tronWebState !== TRON_WEB_STATE_LOCKED) {
      contract = await window.tronWeb.contract().at(contractAddress);
    } else {
      contract = await defaultTronWeb.contract().at(contractAddress);
    }
  } catch (e) {
    console.error(e);
    return null;
  }

  return contract;
};

export const trxToSun = trx => {
  return trx * 1e6;
};

export const sunToTrx = sun => {
  if (isNaN(sun)) {
    return '';
  }
  return sun / 1e6;
};

