import uuid from 'uuid/v4';

const SET_SCREEN = 'SET_SCREEN';

const SCREEN_LANDING = 'SCREEN_LANDING';
const SCREEN_VAULT = 'SCREEN_VAULT';
const SCREEN_VAULT_SUPER = 'SCREEN_VAULT_SUPER';
const SCREEN_VAULT_LIGHTNING = 'SCREEN_VAULT_LIGHTNING';
const SCREEN_ACCOUNT_DETAILS = 'SCREEN_ACCOUNT_DETAILS';
const RELOAD_ACCOUNT = 'RELOAD_ACCOUNT';

export { SET_SCREEN, SCREEN_LANDING, SCREEN_VAULT, SCREEN_VAULT_SUPER, SCREEN_VAULT_LIGHTNING, SCREEN_ACCOUNT_DETAILS, RELOAD_ACCOUNT };

export const setScreen = screen => ({
  type: SET_SCREEN,
  payload: {
    screen,
  },
});

export const reloadAccount = () => ({
  type: RELOAD_ACCOUNT,
  payload: {
    reloadAccountNonce: uuid(),
  },
});
