import React from 'react';
import { connect } from 'react-redux';

import { SCREEN_LANDING, SCREEN_VAULT, SCREEN_VAULT_SUPER,SCREEN_VAULT_LIGHTNING, SCREEN_ACCOUNT_DETAILS } from '../../actions/app';

import Landing from '../TronMax/Landing';
import DailyDivs from '../TronMax/DailyDivs';
import DailyDivsSuper from '../TronMax/DailyDivsSuper';
import DailyDivsLightning from '../TronMax/DailyDivsLightning';
import AccountDetails from '../TronMax/AccountDetails';

const RoutingScreen = ({ screen }) => {
  switch (screen) {
  	case SCREEN_LANDING:
      return <Landing />;
    case SCREEN_VAULT_SUPER:
      return <DailyDivsSuper />;
    case SCREEN_VAULT:
      return <DailyDivs />;
    case SCREEN_VAULT_LIGHTNING:
      return <DailyDivsLightning />;
    case SCREEN_ACCOUNT_DETAILS:
      return <AccountDetails />;
    default:
      return null;
  }
};

const mapStateToProps = state => ({
  screen: state.app.screen,
});

export default connect(mapStateToProps)(RoutingScreen);
