import ReactGA from 'react-ga';
// import { isProductionEnvironment } from './environment';
const isProductionEnvironment = false;

export default {
  initialize: () => {
    isProductionEnvironment && ReactGA.initialize('UA-3373847473-1');
  },
  pageview: (rawPath, trackerNames, title) => {
    isProductionEnvironment && ReactGA.pageview(rawPath, trackerNames, title);
  },
  event: (
    { category, action, label, value, nonInteraction, transport, ...args } = {},
    trackerNames
  ) => {
    isProductionEnvironment &&
      ReactGA.event(
        { category, action, label, value, nonInteraction, transport, ...args },
        trackerNames
      );
  },
};
