import TronWeb from 'tronweb';
import BigNumber from 'bignumber.js';
import store from '../store';
import { showSnackbar } from '../actions/snackbar';
import { reloadAccount } from '../actions/app';
import {
  SNACKBAR_ERROR,
  SNACKBAR_INFO,
} from '../components/Common/GameSnackbar';

import {
  FOUNDATION_ADDRESS,
  initContract,
  injectTronWeb,
  sunToTrx,
  TRON_WEB_STATE_LOCKED,
  TRON_WEB_STATE_NOT_INSTALLED,
  TRON_WEB_STATE_NOT_MAINNET,
  trxToSun,
  weiToToken,
  getDateTimeLongFromUnix,

} from './tronCommon';

const gasLimit = 50 * 1000000; // 50 trx

// MAINNET
const CONTRACT_ADDRESS = '4122d80bb74711e712f3c9d504ac56b687af28331f';
const AIRDROP_CONTRACT_ADDRESS = '4128937030d781d0efa1e86e993bc59efcb225032b'; 
const TRONGRID_API = 'https://api.trongrid.io';


const initDefaultTronWeb = () => {
  const defaultTronWeb = new TronWeb(TRONGRID_API, TRONGRID_API, TRONGRID_API);

  // Set default address (foundation address) used for contract calls
  // Directly overwrites the address object as TronLink disabled the
  // function call
  defaultTronWeb.defaultAddress = {
    hex: defaultTronWeb.address.toHex(FOUNDATION_ADDRESS),
    base58: FOUNDATION_ADDRESS,
  };

  return defaultTronWeb;
};

export const defaultTronWeb = initDefaultTronWeb();

let currentAddress;
let contract;
let airdropContract;
let tronWebState;
const monitorAddressChanged = () => {
  window.tronWeb.on('addressChanged', async address => {
    if (currentAddress !== address.base58) {
      const needsToNotify = !!currentAddress;
      currentAddress = address.base58;

      tronWebState = await injectTronWeb(defaultTronWeb);
      contract = await initContract(
        tronWebState,
        defaultTronWeb,
        CONTRACT_ADDRESS
      );
      airdropContract = await initContract(
        tronWebState,
        defaultTronWeb,
        AIRDROP_CONTRACT_ADDRESS
      );

      if (needsToNotify) {
        store.dispatch(showSnackbar('message.addressChanged', SNACKBAR_INFO));
        store.dispatch(reloadAccount());
      }
    }
  });
};

export default {
  async initTronWeb() {
    tronWebState = await injectTronWeb(defaultTronWeb);
    contract = await initContract(
      tronWebState,
      defaultTronWeb,
      CONTRACT_ADDRESS
    );

    airdropContract = await initContract(
      tronWebState,
      defaultTronWeb,
      AIRDROP_CONTRACT_ADDRESS
    );
    if (!contract || !airdropContract) {
      tronWebState = TRON_WEB_STATE_NOT_MAINNET;
      store.dispatch(
        showSnackbar('message.needToChangeNetwork', SNACKBAR_ERROR)
      );
    }

    monitorAddressChanged();

    if (tronWebState === TRON_WEB_STATE_NOT_INSTALLED) {
      store.dispatch(showSnackbar('message.requireInstall', SNACKBAR_INFO));
    } else if (tronWebState === TRON_WEB_STATE_LOCKED) {
      store.dispatch(showSnackbar('message.requireLogin', SNACKBAR_INFO));
    }
    return tronWebState;
  },

  getCurrentAddress() {
    return window.tronWeb.defaultAddress.base58;
  },

  // Main functions
  async currentAddressBalance() {
    if (tronWebState === TRON_WEB_STATE_LOCKED) {
      return 0;
    }
    const balance = await window.tronWeb.trx.getBalance();
    return balance;
  },

  async getBalance() {
    const balance = await contract.getContractBalance().call();
    return sunToTrx(new BigNumber(balance).toNumber());
  },

  async getPlayerCount() {
    const totalusers = await contract.totalUsers().call();

    const playerCount = totalusers ? new BigNumber(totalusers).toNumber() : 0;    

    return playerCount;
  },

  async getTotalReferrals() {
    const totalReferrals = await contract.getTotalReferrals().call();
    return totalReferrals
      ? sunToTrx(new BigNumber(totalReferrals).toNumber())
      : 0;
  },

  async getTotalInvestments() {
    const totalInvestments = await contract.totalInvested().call();
    return totalInvestments
      ? sunToTrx(new BigNumber(totalInvestments).toNumber())
      : 0;
  },

  async getTotalWithdraws() {
    const totalWithdraws = await contract.totalWithdrawn().call();
    return totalWithdraws
      ? sunToTrx(new BigNumber(totalWithdraws).toNumber())
      : 0;
  },

  async getTotalTxs() {
    const totalTxs = await contract.getTotalTxs().call();
    return totalTxs ? new BigNumber(totalTxs).toNumber() : 0;
  },
  
  async getUserAvailable() {
    const currentAddress = this.getCurrentAddress();
    if (!currentAddress) {
      return 0;
    }
    const userAvailable = await contract.getUserAvailable(currentAddress).call();
    return userAvailable ? sunToTrx(new BigNumber(userAvailable).toNumber()) : 0;
  },

  async getContractBalanceRate() {
    const contractBalanceRate = await contract.getContractBalanceRate().call();
    return (new BigNumber(contractBalanceRate).toNumber());
  },

  async getUserPercentRate() {
    const currentAddress = this.getCurrentAddress();
    if (!currentAddress) {
      return 0;
    }
    const userPercentRate = await contract.getUserPercentRate(currentAddress).call();
    return (new BigNumber(userPercentRate).toNumber());
  },

  async getUserTotalDeposits() {
    const currentAddress = this.getCurrentAddress();
    if (!currentAddress) {
      return 0;
    }
    const userTotalDeposits = await contract.getUserTotalDeposits(currentAddress).call();
    return userTotalDeposits ? sunToTrx(new BigNumber(userTotalDeposits).toNumber()) : 0;
  },

  async getUserTotalWithdrawn() {
    const currentAddress = this.getCurrentAddress();
    if (!currentAddress) {
      return 0;
    }
    const userTotalWithdrawn = await contract.getUserTotalWithdrawn(currentAddress).call();
    return userTotalWithdrawn ? sunToTrx(new BigNumber(userTotalWithdrawn).toNumber()) : 0;
  },

  async getUserReferralBonus() {
    const currentAddress = this.getCurrentAddress();
    if (!currentAddress) {
      return 0;
    }
    const referralBonus = await contract.getUserReferralBonus(currentAddress).call();
    return referralBonus ? sunToTrx(new BigNumber(referralBonus).toNumber()) : 0;
  },

  async getUserReferralCountByLevel(level) {
    const currentAddress = this.getCurrentAddress();
    if (!currentAddress) {
      return 0;
    }
    const referralCount = await contract.getUserReferralCountByLevel(currentAddress, level).call();
    return referralCount ? new BigNumber(referralCount).toNumber() : 0;
  },

  async getUserCheckpoint() {
    const currentAddress = this.getCurrentAddress();
    if (!currentAddress) {
      return 0;
    }
    const userCheckPoint = await contract.getUserCheckpoint(currentAddress).call();
    
    const checkpoint = getDateTimeLongFromUnix(
        new BigNumber(userCheckPoint).toNumber()
      );
    return checkpoint;
  },

  async currentDivsAddressBalance() {
    const balance = await window.tronWeb.trx.getBalance();
    return balance;
  },

  async invest(sendAmount, referrer) {
    // console.log(sendAmount, referrer);
    if (tronWebState === TRON_WEB_STATE_NOT_INSTALLED) {
      store.dispatch(showSnackbar('message.requireInstall', SNACKBAR_ERROR));
      return false;
    } else if (tronWebState === TRON_WEB_STATE_LOCKED) {
      store.dispatch(showSnackbar('message.requireLogin', SNACKBAR_ERROR));
      return false;
    }
    const payAmount = trxToSun(sendAmount);

    const data = await contract.invest(referrer).send({
      feeLimit: gasLimit,
      callValue: payAmount,
      shouldPollResponse: false,
    });
    return data;
  },

  async withdrawDivs() {
    if (tronWebState === TRON_WEB_STATE_NOT_INSTALLED) {
      store.dispatch(showSnackbar('message.requireInstall', SNACKBAR_ERROR));
      return false;
    } else if (tronWebState === TRON_WEB_STATE_LOCKED) {
      store.dispatch(showSnackbar('message.requireLogin', SNACKBAR_ERROR));
      return false;
    }
    const payAmount = 0;
    const data = await contract.withdraw().send({
      feeLimit: gasLimit,
      callValue: payAmount,
      shouldPollResponse: false,
    });
    return data;
  },

  // Airdrop functions
  async getUserWithdrawnAirdrop() {
    const currentAddress = this.getCurrentAddress();
    if (!currentAddress) {
      return 0;
    }
    const withdrawnAirdrop = await airdropContract.getUserWithdrawn(currentAddress).call();
    return withdrawnAirdrop ? weiToToken(new BigNumber(withdrawnAirdrop).toNumber()) : 0;
  },

  async getUserAvailableWithdrawAirdrop() {
    const currentAddress = this.getCurrentAddress();
    if (!currentAddress) {
      return 0;
    }
    const availableAirdrop = await airdropContract.getUserAvailableWithdraw(currentAddress).call();
    return availableAirdrop ? weiToToken(new BigNumber(availableAirdrop).toNumber()) : 0;
  },

  async getAirdropRate() {
    const airdropRate = await airdropContract.getAirdropRate().call();
    return airdropRate ? new BigNumber(airdropRate).toNumber() : 0;
  },

  async getAirdrop() {
    if (tronWebState === TRON_WEB_STATE_NOT_INSTALLED) {
      store.dispatch(showSnackbar('message.requireInstall', SNACKBAR_ERROR));
      return false;
    } else if (tronWebState === TRON_WEB_STATE_LOCKED) {
      store.dispatch(showSnackbar('message.requireLogin', SNACKBAR_ERROR));
      return false;
    }
    const payAmount = 0;
    const data = await airdropContract.withdraw().send({
      feeLimit: gasLimit,
      callValue: payAmount,
      shouldPollResponse: false,
    });
    return data;
  },

  // Util functions
  fromHex(hex) {
    if (hex === '0x0000000000000000000000000000000000000000') {
      return '';
    } else {
      try {
        const base58 = defaultTronWeb.address.fromHex(hex.trim());
        return base58;
      } catch (e) {
        console.error(hex);
        console.error(e);
        return hex;
      }
    }
  },
};
