export const toTruncateDown = (value, n) => {
  if (value < Math.pow(0.1, n)) {
    return 0;
  }
  const v = value.toString().split('.');
  if (n <= 0) {
    return v[0];
  }

  let f = v[1] || '';
  if (f.length > n) {
    const result = `${v[0]}.${f.substr(0, n)}`;
    return result;
  }
  while (f.length < n) {
    f += '0';
  }
  const result = `${v[0]}.${f}`;
  return result;
};
